@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.App {
  width: 100vw;
  height: 100vh;
  background: #ec008c;
  /* fallback for old browsers */
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #fc6767, #ec008c);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */ }

.calculator {
  width: 320px;
  height: 600px;
  background-color: white;
  border-radius: 20px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  text-overflow: ellipsis;
  overflow: hidden; }

.display-calculator {
  height: 180px; }

.output-results {
  font-size: 35px;
  color: #868686; }

.history {
  color: #aaaaaa; }

.button {
  border: 0;
  padding: 0;
  color: #868686;
  background-color: #fafafa;
  font-size: 20px;
  width: 60px;
  height: 60px;
  margin: 5px;
  text-align: center;
  border-radius: 15px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  cursor: pointer; }
  .button:hover {
    background-color: #ebebeb; }

.button-operator {
  background-color: #eaf8f3;
  color: #99dfc6; }
  .button-operator:hover {
    background-color: #dbf7ed; }

.button-equal {
  background-color: #f8dade;
  color: #ea8894; }
  .button-equal:hover {
    background-color: #fdcbd1; }


.display-flex {
  display: -webkit-flex;
  display: flex; }

.space-around {
  -webkit-justify-content: space-around;
          justify-content: space-around; }

.space-between {
  -webkit-justify-content: space-between;
          justify-content: space-between; }

.flex-end {
  -webkit-justify-content: flex-end;
          justify-content: flex-end; }

.align-end {
  -webkit-align-self: flex-end;
          align-self: flex-end; }

.flex-column {
  -webkit-flex-direction: column;
          flex-direction: column; }

.margin-left-20 {
  margin-left: 20px; }

.margin-right-20 {
  margin-right: 20px; }

.margin-top-10 {
  margin-top: 10px; }

.margin-bottom-10 {
  margin-bottom: 10px; }

.margin-bottom-20 {
  margin-bottom: 20px; }

.margin-bottom-40 {
  margin-bottom: 40px; }

.margin-left-auto {
  margin-left: auto; }

.margin-right-auto {
  margin-right: auto; }

.margin-top-auto {
  margin-top: auto; }

.margin-bottom-auto {
  margin-bottom: auto; }

.margin-right-50 {
  margin-right: 50px; }

.text-right {
  text-align: right; }

